.instrumentmenu {
	margin: auto;
	display: flex;
	justify-content: center;
	position: relative;
	border: 1px solid rgba(255,255,255,0.5);
	border-radius: 8px;
}

.btn {
	color: white;
	border: none;
	background: none;	
	display: block;
	width: calc(100% / 2);
	padding: 0 24px;
	margin: 10px;
	text-align: center;
	font-size: 1.2rem;
	cursor: pointer;
	font-weight: bold;
}

.hr {
	transition: all 375ms ease-out;
	will-change: transform, background;
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	margin: 0;
	border: 0;
	border-radius: 8px;
	width: calc(100% / 2);
	background: rgba(255,255,255,0.2);
}

.selected ~ .hr {
	transition: transform 250ms cubic-bezier(0,.5,.5,1.1), background 250ms ease-out;
}


.btn.selected ~ .hr {
	transition: transform 250ms cubic-bezier(0, 0.5, 0.5, 1.1), background 250ms ease-out;
}

.btn:nth-child(1).selected ~ .hr {
	transform: translateX(0%);
}

.btn:nth-child(2).selected ~ .hr {
	transform: translateX(100%);
}